@font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-thin-webfont.eot');
    src: url('fonts/Archia/archia-thin-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-thin-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-thin-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-thin-webfont.ttf') format('truetype');
    font-weight: 100;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-light-webfont.eot');
    src: url('fonts/Archia/archia-light-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-light-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-light-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-light-webfont.ttf') format('truetype');
    font-weight: 200;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-regular-webfont.eot');
    src: url('fonts/Archia/archia-regular-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-regular-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-regular-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-regular-webfont.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-medium-webfont.eot');
    src: url('fonts/Archia/archia-medium-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-medium-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-medium-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-medium-webfont.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-semibold-webfont.eot');
    src: url('fonts/Archia/archia-semibold-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-semibold-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-semibold-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-semibold-webfont.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Archia';
    src: url('fonts/Archia/archia-bold-webfont.eot');
    src: url('fonts/Archia/archia-bold-webfont.eot?#iefix') format('embedded-opentype'),       
    url('fonts/Archia/archia-bold-webfont.woff2') format('woff2'),       
    url('fonts/Archia/archia-bold-webfont.woff') format('woff'),       
    url('fonts/Archia/archia-bold-webfont.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
  }
  
  /* -- */
  
  $blue: #007bff;
  $blue-light: #007bff10;
  $blue-hover: #007bff30;
  $blue-light-opaque: #eff7ff;
  $blue-mid: #007bff99;
  $indigo: #6610f2;
  $purple: #6f42c1;
  $pink: #e83e8c;
  $cherry: #ff073a;
  $cherry-mid: #ff073a99;
  $cherry-light: #ff073a20;
  $cherry-hover: #ff073a30;
  $cherry-light-opaque: #ffe0e6;
  $red: #dc3545;
  $red-light: #dc354520;
  $red-mid: #dc354599;
  $orange: #fd7e14;
  $orange-mid: #fd7e1499;
  $orange-light: #fd7e1420;
  $orange-hover: #fd7e1430;
  $orange-light-opaque: #ffefe2;
  $yellow: #ffc107;
  $yellow-light: #ffc10720;
  $yellow-hover: #ffc10730;
  $yellow-mid: #ffc10799;
  $yellow-light-opaque: #fff7e0;
  $green: #28a745;
  $green-light: #28a74520;
  $green-hover: #28a74530;
  $green-mid: #28a74599;
  $teal: #20c997;
  $cyan: #17a2b8;
  $white: #fff;
  $gray: #6c757d;
  $gray-light: #6c757d10;
  $gray-hover: #6c757d20;
  $gray-mid: #6c757d99;
  $gray-dark: #343a40;
  $gray-opaque: #343a4020;
  $primary: #007bff;
  $secondary: #6c757d;
  $success: #28a745;
  $info: #17a2b8;
  $warning: #ffc107;
  $danger: #dc3545;
  $light: #f8f9fa;
  $dark: #343a40;
  $purple: #201aa2dd;
  $purple-light: #201aa220;
  $purple-hover: #201aa230;
  $purple-mid: #201aa299;
  $purple-light-opaque: #e3e2f3;
  
  @mixin navbarReveal($color) {
    $animation-name: unique-id() !global;
  
    animation-name: $animation-name;
    animation-duration: 0.750s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
  
    @keyframes #{$animation-name} {
      0% {
        background: transparent;
      }
      100% {
        background: $color;;
      }
    }
  }
  
  @mixin levelColor($color) {
    .is-#{$color} {
      h1 {color: $color;}
      h4, h5 {color: #{$color}-mid;}
    }
  }
  
  body {
    padding: 0;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
  }
  
  h1, {
    font-family: 'archia';
    font-size: 24px !important;
    font-weight: 900;
    text-transform: uppercase;
  }
  
  h2 {
    font-family: 'archia';
    font-size: 18px !important;
    font-weight: 400;
    text-transform: uppercase;
  }
  
  h3 {
    font-family: 'archia';
    font-size: 14px !important;
    font-weight: 400;
    text-transform: uppercase; 
  }
  
  h4 {
    font-family: 'archia';
    font-size: 13px !important;
    font-weight: 900;
    text-transform: uppercase; 
  }
  
  h5 {
    font-family: 'archia';
    font-size: 12px !important;
    font-weight: 900;
    text-transform: uppercase; 
  }
  
  h6 {
    font-family: 'archia';
    font-size: 11px !important;
    font-weight: 600;
    text-transform: uppercase; 
  }
  
  .Home {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-left: 10rem;
    margin-right: 10rem;
    margin-top: 2rem;
    .home-left, .home-right  {
      display: flex;
      flex-direction: column;
      width: 30rem;
    }
    .home-right {
      max-height: 600px !important;
      overflow: auto;
    }
  }
  
  .Navbar {
    display: flex;
    flex-direction: row;
    height: 4.5rem;
    justify-content: space-around;
    text-transform: uppercase;
    font-weight: 900;
    @include navbarReveal($gray-light);
    .navbar-left {
      margin-left: 2rem;
      flex: 1;
      align-self: center;
      a {
        display: inline-block;
        font-size: 14px;
        font-family: 'Archia';
        text-decoration: none;
        margin-right: 2rem;
        color: $gray-mid;
        span {
          display: inline-block;
          &:hover {
            color: $gray;
          }
        }
      }
    }
    .focused {
      color: $gray;
    }
    img {
      width: 3rem;
      height: 3rem;
      align-self: center;
      margin-left: 5rem;
      cursor: pointer;
    }
  }
  
  .header {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 30rem;
    align-self: center;
    justify-content: space-between;
    padding-top: 2.5rem;
    margin-bottom: 1rem;
    h1, h6 {
      margin: 0;
    }
    h1 {
      color: $gray-dark;
      margin-bottom: 1rem;
    }
    .header-mid {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      & > * {
        align-self: center;
      }
    }
  }
  
  .last-update {
    display: flex;
    flex-direction: column;
    align-self: center;
    text-align: right;
    margin-bottom: auto;
    margin-top: 0.25rem;
    h6 {
      color: $green-mid;
      font-weight: 900;
    }
    h3 {
      color: $green;
      font-weight: 600;
    }
    h3, h6 {
      margin: 0;
    }
  }
  
  a.button {
    display: flex;
    flex-direction: row;
    padding: 0.5rem;
    border: none;
    font-family: 'archia';
    text-transform: uppercase;
    background: $blue-light;
    color: $blue-mid;
    cursor: pointer;
    font-weight: 900;
    border-radius: 5px; 
    transition: background 0.2s ease-in-out;
    margin-bottom: 0.5rem;
    margin-top: 0.25rem;
    outline: none;
    font-size: 0.75rem;
    text-decoration: none;
    svg {
      margin-right: 0.5rem;
      width: 20px;
      stroke-width: 2px;
    }
    &.is-purple {
      color: $purple-mid;
      background: $purple-light;
      &:hover {
        background: $purple-hover;
      }
    }
      &.is-green {
      color: $green-mid;
      background: $green-light;
      &:hover {
        background: $green-hover;
      }
    }
    &:hover {
    background: $blue-hover;
    }
    & > * {
      align-self: center;
    }
  }
  
  .telegram {
    width: 15.5rem;
    background: $gray-light !important;
    color: #0088cc !important;
    padding-left: 0.65rem;
    &:hover {
      background: $gray-hover !important;
    }
  }
  
  .excel {
    color: #33A667;
    background: #33A66730;
    &:hover {
      background: #33A66750;
    }
  }
  
  .Level {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-self: center;
    width: 30rem;
    .level-item {
      display: flex;
      flex-direction: column;
      & > * {
        align-self: center;
      }
      &:first-child {
        margin-left: 0.5rem;
      } 
      &:last-child {
        margin-right: 0.5rem;
      }
    }
    h1, h5 {
      margin-bottom: 0;
    }
    h1 {
      font-weight: 600;
    }
    h4 {
      margin-bottom: -0.5rem;
    }
    .is-cherry {
      h1 {color: $cherry;}
      h4, h5 {color: $cherry-mid;}
    }
    .is-blue {
      h1 {color: $blue;}
      h4, h5 {color: $blue-mid;}
    }
    .is-green {
      h1 {color: $green;}
      h4, h5 {color: $green-mid;}
    }
    .is-gray {
      h1 {color: $gray;}
      h4, h5 {color: $gray-mid;}
    }
  }
  
  abbr {
    text-decoration: none;
    &.is-cherry {
      color: $cherry;
    }
    &.is-blue {
      color: $blue;
    }
    &.is-green {
      color: $green;
    }
    &.is-gray {
      color: $gray;
    }
  }
  
  table {
    position: relative;
    width: 30rem;
    align-self: center;
    font-family: 'archia';
    text-transform: uppercase;
    margin-bottom: 1rem;
    margin-top: 1rem;
    height: 400px;
    overflow: scroll;
    thead {
      background: $gray-light;
      color: $gray-dark;
      text-align: left;
      font-size: 0.75rem;
      th {
        padding: 0.5rem;
        cursor: pointer;
        border-radius: 5px;
        transition: all 0.1s ease-in-out;
        &:hover {
          background: $gray-hover;
        }
        background: #f6f6f7;
        border-radius: 5px;
        cursor: pointer;
        padding: .5rem;
        position: sticky!important;
        top: 5px;
        transition: all .1s ease-in-out;
        z-index: 99;
        .heading-content {
          display: flex;
          justify-content: space-between;
          position: relative;
          flex-direction: row;
          height: 0.9rem;
          svg {
            width: 10px;
            margin: 0;
            right: 0;
            transform: rotate(135deg);
            stroke-width: 4px;
            align-self: center;
            margin-top: -0.35rem;
            margin-left: 0.25rem;
            color: $gray-mid;
          }
        }
      }
    }
    tbody {
      color: $gray;
      tr {
        transition: background 0.1s ease-in-out;
        &:hover {
          background: $gray-hover;
        }
      }
      td {
        padding: 0.25rem;
        font-size: 0.9rem;
        border-radius: 5px;
      }
    }
    .affected-count {
      position: absolute;
      margin: 0;
      top: -1rem;
      color: $gray-mid;
      right: 0;
    }
    .is-total {
      td {
        background: $gray-hover;
      }
    }
  }

  .newCard {
    h1, h5, h6 {
      margin: 0;
      color: #000;
    }
    h4 {
      color: #e23028;
      width: 5rem;
    }
    h1 {
      font-weight: 600;
    }
  }
  
  .ChoroplethMap {
    display: flex;
    position: relative;
    align-self: center;
    margin-top: 4rem;
    margin-bottom: -10rem;
    width: 30rem;
    .header {
      position: absolute;
      top: -5rem;
      color: $gray-dark;
    }
    h6.header {
      margin-top: 3rem;
      color: $gray;
      margin-left: 1px;
      width: 10rem;
    }
    .svg-parent {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      svg {
        align-self: center ;
        text {
          text-transform: uppercase;
          text-align: right;
          font-family: 'archia';
          font-weight: 600;
          fill: $gray;
          font-size: 14px;
        }
      }
    }
    .map-stats {
      flex: 0.1;
      position: absolute;
      top: 1.25rem;
      left: 1px;
      h1, h5, h6 {
        margin: 0;
      }
      h4 {
        color: #e23028;
        width: 5rem;
      }
      h1 {
        font-weight: 600;
      }
      .stats {
        background: $cherry-light;
        color: $cherry;
        padding: 0.5rem;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        margin-bottom: 0.5rem;
        .stats-bottom {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          h6 {
            margin-top: auto;
            margin-bottom: 0.25rem;
          }
        }
        &.is-green {
          background: $green-light;
          h5, h6 {color: $green-mid;}
          h1 {color: $green;}
        }
        &.is-blue {
          background: $blue-light;
          h5, h6 {color: $blue-mid;}
          h1 {color: $blue;}
        }
        &.is-gray {
          background: $gray-light;
          h5, h6 {color: $gray-mid;}
          h1 {color: $gray;}
        }
      }
    }
  }
  
  .cMap {
    display: flex;
    width: 100%;
  }
  
  .tabs {
    position: relative;
    z-index: 99;
    display: flex;
    flex-direction: row;
    .tab {
      min-width: 5rem;
      background: $gray-light;
      padding-left: 1rem;
      padding-right: 1rem;
      color: $gray-mid;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      cursor: pointer;
      text-align: center;
      margin-right: 0.25rem;
      transition: all 0.3s ease-in-out;
      &:hover {
        background: $gray-hover;
      }
      &.focused {
        color: $gray;
        background: $gray-opaque;
      }
    }
  }
  
  .TimeSeries-Parent {
    display: flex;
    flex-direction: column;
    width: 30rem;
    align-self: center;
  }
  
  .timeseries-header {
    h1 {
      color: $gray-dark;
      margin: 0;
      margin-bottom: 0.5rem;
    }
    width: 30rem;
    align-self: center;
    margin-top: 1rem;
    .timeseries-mode {
      position: absolute;
      display: flex;
      flex-direction: row;
      top: 4.5rem;
      right: 0;
      z-index: 99;
      input {margin: 0; cursor: pointer;}
      label {
        font-family: 'archia';
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 900;
        margin-right: 0.5rem;
        color: $gray;
        z-index: 99;
      }
    }
  }
  
  
  .timeseries, .Minigraph {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    width: 100%;
    .stats {
      padding: 0.25rem;
      position: absolute;
      top: 0.5rem;
      left: 0.5rem;
      width: 5rem;
      border-radius: 3px;
      display: flex;
      flex-direction: column;
      h2, h5, h6 {
        margin: 0;
        color: $cherry-mid;
      }
      h2, h6 {
        color: $cherry;
      }
      .stats-bottom {
        display: flex;
        flex-direction: row;
        h6 {
          margin-bottom: 0.1rem;
          margin-left: 0.25rem;
        }
        & > * {
          margin-top: auto;
        }
      }
      &.is-green {
        h5 {color: $green-mid;}
        h2, h6 {color: $green;}
      }
      &.is-gray {
        h5 {color: $gray-mid;}
        h2, h6 {color: $gray;}
      }
      &.is-blue {
        h5 {color: $blue-mid;}
        h2, h6 {color: $blue;}
      }
    }
    .svg-parent {
      position: relative;
      display: flex;
      width: 30rem;
      align-self: center;
      background: $cherry-light;
      border-radius: 5px;
      margin-bottom: 1rem;
      svg {
        .domain, .tick, line {
          stroke: $cherry;
          stroke-width: 2;
        }
        text {  
          font-family: 'archia';
          text-transform: uppercase;
          color: $cherry-mid;
          stroke: transparent;
          font-size: 12px;
        }
      }
      &.is-green {
        background: $green-light;
        svg{
          .domain, .tick, line {
            stroke: $green;
          }
          text {
            color: $green-mid;
            stroke: transparent;
          }
        }
      }
      &.is-gray {
        background: $gray-light;
        .domain, .tick, line {
          stroke: $gray;
        }
        text {
          color: $gray-mid;
          stroke: transparent;
        }
      }
    }
  }
  
  .Minigraph {
    width: 30rem;
    align-self: center;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .svg-parent {
      width: 5rem;
      background: transparent !important;
    }
    margin-bottom: -3rem;
    .tooltip {
      position: fixed;
      top: 0;
      right: 0;
    }
  }
  
  .Links {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .link {
    width: 30rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    align-self: center;
    a {
      font-family: 'archia';
      text-transform: uppercase;
      word-wrap: break-word;
      font-weight: 900;
      text-decoration: none;
      color: $blue;
      background: $blue-light;
      transition: background 0.2s ease-in-out;
      &:hover {
        background: $blue-hover;
      }
    }
    h3 {
      margin-bottom: 0;
      color: $gray-dark;
      font-weight: 900;
    }
  }
  
  footer {
    display: flex;
    flex-direction: column;
    margin-bottom: 5rem;
    justify-content: center;
    margin-top: 5rem;
    & > * {
      align-self: center;
      text-align: center;
    }
    h5 {margin: 0; margin-top: 1rem; margin-bottom: 0.5rem; color: $gray;}
    img {
      width: 2rem;
      height: 2rem;
    }
  }
  
  .Summary {
    display: flex;
    flex-direction: column;
    width: 600px;
    height: 600px;
    position: relative;
    padding-left: 1rem;
    padding-right: 1rem;
    img {
      width: 2rem;
      height: 2rem;
    }
    .link {
      margin: 0;
    }
    .header {
      h1 {
        margin-top: -1rem;
      }
    }
    h5 {
      margin: 0;
      color: $gray;
    }
    .Minigraph {
      align-self: center;
      margin-bottom: -3.25rem
    }
    .summary-bottom {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-self: center;
      width: calc(100% - 0rem);
      .link {
        width: 12rem;
        margin-right: -0.75rem;
      }
      & > * {
        align-self: center;
      }
      img {
        margin-bottom: 0.25rem;
        margin-right: 0.25rem;
        margin-left: 1rem;
      }
      h5 {
        width: 15rem;
      }
    }
    .summary-bottom-left {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      & > * {
        align-self: center;
      }
    }
  }
  
  @media (max-width: 769px) {
    .ChoroplethMap, .TimeSeries, table, .header, .Level, .timeseries-header, .TimeSeries-Parent, .Links, .Minigraph, .Summary, {
      width: calc(100% - 2rem);
    }
    .Home {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-left: 0rem;
      margin-right: 0rem;
      .home-left, .home-right {
        width: 100%;
        display: flex;
        flex-direction: column;
      }
      .home-right {
        max-height: 400px !important;
        overflow: auto;
      }
    }
    .link {
      width: calc(100% - 2rem);
    }
    .Navbar {
      a {
        font-size: 0.75rem !important;
        margin-right: 0.75rem !important;
      }
      img {
        width: 2rem;
        height: 2rem;
        margin-left: 1rem;
        margin-right: -1rem;
        cursor: pointer;
      }
    }
    .timeseries {
      .svg-parent {
        width: 100%;
      }
    }
    .last-update {
      width: 10rem;
    }
    a.button {
      width: 10rem;
      svg {
        width: 38px;
      }
    }
  }
  
  /* For the iPhone X folks OMG WHY ARE YOUR SCREENS SO NARROW T_T*/ 
  @media (max-width: 375px) {
    .timeseries-mode {
      display: block !important;
      width: 4.25rem;
      top: 2rem !important;
      text-align: right;
      input {
        margin: 0;
        margin-top: 0.25rem !important;
      }
    }
  }
  
  /* Animation Support */
  
  .fadeInUp {
    animation-name: fadeInUp;
    animation-duration: 0.450s;
    animation-fill-mode: both;
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
  
  .iframe-container{
    width: 100%;
    min-height : 100vh;
    iframe {
      width: 100vw;
      height: 100vh;
      border: none;
    }
  }
  